import React from 'react';
import {BrowserRouter as Router, Route} from 'react-router-dom';

import Navigation from '../Navigation';
import Page_Landing from '../Landing';
import Page_SignIn from '../SignIn'
import Page_SignUp from '../SignUp';
import Page_PasswordForget from '../PasswordForget'
import Page_Home from '../Home';
import Page_Account from '../Account';
import Page_Admin from '../Admin';

import * as ROUTES from '../../constants/routes';

const App = () => {
    return(
        <div>
            <Router>
                <div>
                    <Navigation/>

                    <hr/>

                    <Route exact path={ROUTES.LANDING} component= {Page_Landing} />
                    <Route path={ROUTES.SIGN_UP} component= {Page_SignUp} />
                    <Route path={ROUTES.SIGN_IN} component= {Page_SignIn} />
                    <Route path={ROUTES.PASSWORD_FORGET} component= {Page_PasswordForget} />
                    <Route path={ROUTES.HOME} component= {Page_Home} />
                    <Route path={ROUTES.ACCOUNT} component= {Page_Account} />
                    <Route path={ROUTES.ADMIN} component= {Page_Admin} />
                </div>
            </Router>
        </div>
    )
}

export default App;